
.comments h3 {
	font-size: calc(7px + 15 * (100vw / 1400));
	padding: 5% 0;
}

.comments {
	grid-column: 2 / 3;
}

.comment {
	padding: 1% 0;
	list-style-type: disc;
	list-style-position: inside;
}

.comments__list {
	text-align: left;
	list-style-type: disc;
	list-style-position: inside;
}
