
.single__product__page {
	padding: 2% 0;
	font-family: 'Comfortaa', sans-serif;
	font-size: calc(4px + 12 * (100vw / 1400));
}

.single__product__content {
	padding: 10% 10% 10% 10%;
	display: grid;
	grid-template: repeat(2, 5vh) 1fr / repeat(2, 50%);
	text-align: left;
	position: relative;
}

.single__product__title {
	display: flex;
	padding: 2% 10%;
	justify-content: space-between;
	align-items: center;
}

.single__product__page img {
	grid-row: 1 / 3;
	width: 20vw;
	height: 20vw;
	position: sticky;
	top: 10%;
	left: 0;
	margin: 0;
}